.body1 {
    display: block;
    width: 100%;
   }

.body1 {
    text-align: center;;
}

.body1 img{
    max-width: 100%;
    text-align: center;   
   }

.bottom-left {
    position: absolute;
    top: 50%;
    left: 30%;
    transform: translate(-50%, -50%);
    color: var(--color-primary);
  }

  @media screen and (max-width: 1000px) {

    .body1 {
        min-height: 60vh;
        background-position: top;
        background-size: cover;
        padding: 4rem;
        background-image: url(../img/labsci.jpg);
    }

    .body1 img {
        display: none;
    }

    .bottom-left {
        position: absolute;
        top: 45%;
        left: 40%;
        transform: translate(-50%, -50%);
        color: var(--color-primary);
    }
}